.sorting{
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
}
.item{
    color: #5d5d64;
    font-size: 16px;
    cursor: pointer;
    margin-left: 10px;
}

.item:first-child{
    margin-left: 0;
}
.itemActive{
    color: #151528;
    font-weight: 700;
}